import React from 'react';
import { useState } from 'react';
import Modal from 'react-modal/lib/components/Modal';
import { MainContext, useContext } from "./context"

export default function ModalPopup() {
    const { modalIsOpen, setIsOpen } = useContext(MainContext)

    function openModal() {
        setIsOpen(true);
    }

    function closeModal() {
        setIsOpen(false);
    }

    return (
        <div>
            <Modal
                isOpen={modalIsOpen}
                onRequestClose={closeModal}
                className="contentModal"
                ariaHideApp={false}
            >
                <div>
                    <div className='header'>
                        <div className='title'><strong>NASIL OYNANIR</strong></div>
                        <div className='close'>
                            <button onClick={closeModal}>
                                x
                            </button>
                        </div>
                    </div>
                    <div><p><strong>WORDLE</strong>'i 6 denemede bulun.</p></div>
                    <div><p>Her tahmin 5 harfli doru bir kelime olmaldr. Gndermek iin enter'a basn.</p></div>
                    <div><p>Her tahminden sonra kutucuklarn renkleri tahmininizin yaknlna gre deiecektir.</p></div>
                    <hr />
                    <div><strong><p>rnekler</p></strong></div>
                    <div className='box green'>A</div>
                    <div className='box'>B</div>
                    <div className='box'>O</div>
                    <div className='box'>N</div>
                    <div className='box'>E</div>
                    <div><p><strong>A</strong> harfi kelimede var ve doru yerde.</p></div>
                    <div className='box'>G</div>
                    <div className='box yellow'></div>
                    <div className='box'>Z</div>
                    <div className='box'>E</div>
                    <div className='box'>M</div>
                    <div><p><strong></strong> harfi kelimede var fakat yanl yerde.</p></div>
                    <div className='box'>F</div>
                    <div className='box'>A</div>
                    <div className='box'>L</div>
                    <div className='box'>E</div>
                    <div className='box gray'>Z</div>
                    <div><p><strong>Z</strong>  harfi kelimede yok. </p></div>
                    <hr />
                    <div><strong>Snrsz WORDLE oyna (:</strong></div>
                </div>

            </Modal>
        </div>
    );
}
